@import "styling/variables";

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $white;
  padding: 0rem 1rem;
  min-height: 82px;

  img {
    width: 75px;
  }
}

.buttons {
  display: flex;

  a,
  button {
    box-shadow: none;
    margin-left: 0.5rem;
  }
}
